import React from 'react';
import millify from 'millify';
import { Typography, Row, Col, Statistic } from 'antd';
import { Link} from 'react-router-dom';

import { useGetCryptosQuery } from '../services/cryptoApi';
import {Cryptocurrencies, News} from '../components'
const {Title} = Typography;

const Homepage = () => {
  const { data, isFetching} = useGetCryptosQuery(10);
  const globalStats = data?.data?.stats;

  if(isFetching) return 'Loading.....';
  return (
    <>
    <Title level={1} className="heading">Crypto Stats Global</Title>
    <Row>
      <Col span={12}><Statistic title="Total CryptoCurrencies" value={globalStats.total}/></Col>
      <Col span={12}><Statistic title="Total Exchanges" value={millify(globalStats.totalExchanges)}/></Col>
      <Col span={12}><Statistic title="Total MarketCap" value={millify(globalStats.totalMarketCap)}/></Col>
      <Col span={12}><Statistic title="Total 24h volume" value={millify(globalStats.total24hVolume)}/></Col>
      <Col span={12}><Statistic title="Total Total Markets" value={millify(globalStats.totalMarkets)}/></Col>
      
    </Row>
    
    <div className="home-heading-container">
      <Title level={3} className='htitle'>Top 10 CryptoCurrencies in the GAME</Title>
      <Title level={3} className="show"> <Link to="/cryptocurrencies">Show More</Link></Title>
    </div>
    <Cryptocurrencies simplified/>
    <div className="home-heading-container">
      <Title level={2} className='htitle'>LATEST CRYPTO UPDATES</Title>
      <Title level={3} className="show"> <Link to="/news">Show More</Link></Title>
    </div>
    <News simplified/>
    </>
  )
}

export default Homepage